* {
  margin: 0;
  padding: 0;
  background-color: #fbffcd;
}
/* --------App js------------------ */

.crud {
  text-align: center;
  position: relative;
}

.crud-read {
  position: relative;
  text-align: left;
}

.crud-delete {
  position: relative;
}

h3 {
  margin-left: 20%;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-align: left;
}
/* ------------Create js--------------- */

h1 {
  color: #173460;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-align: center;
}

form {
  margin-top: 2%;
  margin-left: auto;
  margin-right: auto;
  width: 300px;
  height: 300px;
  border-radius: 10px;
  background-color: #cde1ff;
}
#inp {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  border-radius: 2px;
  background-color: #cde1ff;
  margin-left: 20%;
}
#sub {
  font-family: sans-serif;
  color: white;
  width: 100px;
  background-color: black;
  margin-left: 29%;
  box-shadow: 1px 2px 1px 1px gray;
  border-radius: 2px;
}
/* -------------------Read js--------------------------- */
.getData {
  margin-left: 20%;
  padding: 10px;
  height: 800px;
  width: 800px;
}

table {
  border: 1px solid black;
}

td,
th {
  border: 2px solid black;
  width: 100px;
  height: 30px;
}
tr > :nth-child(4) {
  color: black;
  width: 350px;
}
#getButton {
  height: 30px;
  width: 80px;
  font-size: larger;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;

  background-color: #cde1ff;
  color: black;
  margin-left: 30%;
}

#getButton:hover {
  box-shadow: 1px 2px 1px 1px black;
}

/* ------------- */
